	export default {
		data() {
			return {
				totalNum:0,
				type:'',//健康商城 community  服务站商城  customer
				hasLogin: true,
				communityId:'',
				goodsPrice:0,//商品价格
				total: 0, //总价格 （包含运费或者优惠）
				allChecked: false, //全选状态  true|false
				empty: true, //空白页现实  true|false
				cartList: [],
				options2: [{
					text: '删除',
					style: {
						backgroundColor: '#dd524d'
					}
				}],
				expenses:0,//运费金额
				minimum:0,//免邮金额
                instanceId:null
			};
		},
        onShow() {
            this.getPostFee(this.type)
            this.loadData()
        },
		onLoad(options) {
            if(options.instanceId){
                this.instanceId = options.instanceId
            } 
			this.type =options.type
			this.communityId = options.communityId
			this.getPostFee(options.type)
			this.loadData()
		},
		watch: {
			//显示空白页
			cartList(e) {
				let empty = e.length === 0 ? true : false;
				if (this.empty !== empty) {
					this.empty = empty;
				}
			}
		},
        filters: {
        	toFixed2: function(value) {
        		return Number(value).toFixed(2);
        	},
        },
		computed: {

		},
		methods: {
			change(e) {
				this.isOpened = e
				console.log('返回：', e);
			},
			bindClick(index,e) {
				if(e.index==0){
					this.deleteCartItem(index)
				}
			},
			loadData() {
				uni.getStorage({
					key : `${this.type}_cartList`,
					success:(res)=> {
						// console.log(res.data);
						let asyncCartList = res.data
						let tempArr = asyncCartList.map(item => {
							item.checked = true;
							if(!item.stock){
								item.stock = 999;
							}
							return item;
						});
						this.cartList = tempArr;
					},
					fail:(err)=> {
						console.log(err);
						this.cartList = []
					}
				})
			},
			//监听image加载完成
			onImageLoad(key, index) {
				this.$set(this[key][index], 'loaded', 'loaded');
			},
			//监听image加载失败
			onImageError(key, index) {
				this[key][index].image = '/static/healthy-mall/errorImage.jpg';
			},
			//选中状态处理
			check(type, index) {
				if (type === 'item') {
					this.cartList[index].checked = !this.cartList[index].checked;
				} else {
					const checked = !this.allChecked
					const list = this.cartList;
					list.forEach(item => {
						item.checked = checked;
					})
					this.allChecked = checked;
				}
				this.calcTotal(type);
			},
			//数量
			numberChange(index,value) {
				this.cartList[index].number = parseInt(value);
				uni.setStorageSync(`${this.type}_cartList`,this.cartList)
				this.calcTotal();
			},
			//删除
			deleteCartItem(index) {
				this.cartList.splice(index, 1);
				uni.setStorageSync(`${this.type}_cartList`,this.cartList)
				this.calcTotal();
				uni.hideLoading();
			},
			//清空
			clearCart() {
				uni.showModal({
					content: '清空购物车？',
					success: (e) => {
						if (e.confirm) {
							this.cartList = [];
						}
					}
				})
			},
			//计算总价
			calcTotal() {
				let list = this.cartList;
				if (list.length === 0) {
					this.empty = true;
					return;
				}
				let total = 0;
				let goodsPrice = 0
				let totalNum = 0
				let checked = true;
				list.forEach(item => {
					if (item.checked === true) {
						goodsPrice += item.price * item.number;
						totalNum += item.number;
					} else if (checked === true) {
						checked = false;
					}
				})
				this.goodsPrice = goodsPrice
				this.allChecked = checked;
				if(this.minimum-goodsPrice>0){
					total=this.expenses+goodsPrice
				}else{
					total=goodsPrice
				}
				this.totalNum = Number(totalNum).toFixed(0)
				this.total = Number(total).toFixed(2);
			},
			//创建订单
			createOrder() {
				let list = this.cartList;
				let tmpList = [];
				list.forEach(item => {
					if (item.checked) {
						tmpList.push(item)
					}
				})
				if (tmpList.length < 1) {
				    uni.showToast({
				        title: '请选择商品结算',
				        icon: 'none'
				    });
				    return;
				}
				console.log(tmpList);
				let fee = this.minimum - this.goodsPrice>0 ? this.expenses:0
                
				uni.setStorage({
				    key: 'buylist',
				    data: tmpList,
				    success: () => {
                        
				        uni.navigateTo({
				            url: `/pages/pay/confirm/confirm?type=${this.type}&fee=${fee}&communityId=${this.communityId}&instanceId=${this.instanceId}`
				        })
				    }
				})
			},
			getPostFee(type){
				// 健康商城 community  服务站商城  customer
				switch (type){
					case 'community':
						this.$api.communityGetPostage().then(res=>{
							if(res.status=="OK"){
								if(res&&res.data&&res.data.expenses){
									this.expenses = res.data.expenses/100
									this.minimum = res.data.minimum/100
								}
								this.calcTotal(); //计算总价
							}
						}).catch(err=>{
							console.log(err);
						})
						break;
					case 'customer':
						this.$api.customerGetPostage().then(res=>{
							if(res.status=="OK"){
								if(res&&res.data&&res.data.expenses){
									this.expenses = res.data.expenses/100
									this.minimum = res.data.minimum/100
								}
								this.calcTotal(); //计算总价
							}
						}).catch(err=>{
							console.log(err);
						})
						break;
				}
				
			}
		}
	}
